import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Task, TasksRoot } from '../models/tasks.model';

export interface CreateTaskRequest {
  title: string;
  description?: string;
  deptId: number;
  assigneeUserId: string;
  priority: number;
  dueDate: string;
  sourceType: number;
  sourceId?: number;
}

export interface UpdateTaskRequest extends CreateTaskRequest {}

export interface TaskStatusUpdate {
  status: number;
  note: string;
}

export interface BulkStatusUpdate {
  bulkCloseDto: {
    taskIds: number[];
    status: number;
    note: string;
  };
}

export interface BulkReassignUpdate {
  taskIds: number[];
  newAssigneeUserId: string;
  note: string;
}

export interface TaskUpdate {
  note: string;
}

export interface TaskAttachment {
  id: number;
  fileName: string;
  fileType: string;
  description?: string;
  uploadedAt: string;
  uploadedByName: string;
  fileSize?: number;
}

export interface TaskUpdateItem {
  id: number;
  note: string;
  createdByName: string;
  createdAt: string;
}

export interface TasksOverview {
  totalTasks: number;
  completedTasks: number;
  pendingTasks: number;
  overdueTasks: number;
  completionRate: number;
  statusDistribution: StatusDistribution[];
  priorityDistribution: PriorityDistribution[];
}

export interface StatusDistribution {
  status: string;
  count: number;
  percentage: number;
}

export interface PriorityDistribution {
  priority: string;
  count: number;
  percentage: number;
}

@Injectable({
  providedIn: 'root'
})
export class TasksService {
  private apiUrl = `${environment.apiBaseUrl}/Tasks`;
  
  constructor(private http: HttpClient) {}

  getAllTasks(page: number = 1, pageSize: number = 10): Observable<TasksRoot> {
    return this.http.get<TasksRoot>(`${this.apiUrl}?page=${page}&pageSize=${pageSize}`);
  }

  getAllTasksForCounting(): Observable<TasksRoot> {
    // Get all tasks without pagination for counting by status
    return this.http.get<TasksRoot>(`${this.apiUrl}?page=1&pageSize=1000`);
  }

  getTasksOverview(): Observable<TasksOverview> {
    return this.http.get<TasksOverview>(`${environment.apiBaseUrl}/Dashboard/tasks/overview`);
  }

  getTaskById(id: number): Observable<any> {
    return this.http.get<any>(`${this.apiUrl}/${id}`);
  }

  createTask(task: CreateTaskRequest): Observable<any> {
    return this.http.post<any>(this.apiUrl, task);
  }

  updateTask(id: number, task: UpdateTaskRequest): Observable<any> {
    return this.http.put<any>(`${this.apiUrl}/${id}`, task);
  }

  deleteTask(id: number): Observable<any> {
    return this.http.delete<any>(`${this.apiUrl}/${id}`);
  }

  updateTaskStatus(id: number, statusUpdate: TaskStatusUpdate): Observable<any> {
    return this.http.post<any>(`${this.apiUrl}/${id}/status`, statusUpdate);
  }

  bulkUpdateStatus(bulkUpdate: BulkStatusUpdate): Observable<any> {
    return this.http.post<any>(`${this.apiUrl}/bulk/status`, bulkUpdate);
  }

  bulkReassign(bulkReassign: BulkReassignUpdate): Observable<any> {
    return this.http.post<any>(`${this.apiUrl}/bulk/reassign`, bulkReassign);
  }

  getTaskUpdates(id: number): Observable<any> {
    return this.http.get<any>(`${this.apiUrl}/${id}/updates`);
  }

  addTaskUpdate(id: number, update: TaskUpdate): Observable<any> {
    return this.http.post<any>(`${this.apiUrl}/${id}/updates`, update);
  }

  getTaskAttachments(id: number): Observable<any> {
    return this.http.get<any>(`${this.apiUrl}/${id}/attachments`);
  }

  addTaskAttachment(id: number, attachment: FormData): Observable<any> {
    return this.http.post<any>(`${this.apiUrl}/${id}/attachments`, attachment);
  }

  deleteAttachment(taskId: number, attachmentId: number): Observable<any> {
    return this.http.delete<any>(`${this.apiUrl}/${taskId}/attachments/${attachmentId}`);
  }
}
